import React, { useState, useEffect } from "react";
import { Modal } from 'react-bootstrap';
import axios from "axios";

import './DetailModalUser.css';

const DetailModalUser = props => {
    return (
        <div>
            <Modal dialogClassName='modal-custom' show={props.showDetail} onHide={props.closeDetailModalHandler()}>
                <Modal.Header closeButton>
                    <Modal.Title>Chi tit ti khon</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <table className="product-details">
                        <tbody>
                            <tr>
                                <td>ID:</td>
                                <td>{props.user._id}</td>
                            </tr>
                            <tr>
                                <td>Tn:</td>
                                <td>{props.user.lastName}</td>
                            </tr>
                            <tr>
                                <td>H:</td>
                                <td>{props.user.firstName}</td>
                            </tr>
                            <tr>
                                <td>Gii tnh:</td>
                                <td>{props.user.gender}</td>
                            </tr>
                            <tr>
                                <td>Ngy sinh:</td>
                                <td>{props.user.dob}</td>
                            </tr>
                            <tr>
                                <td>Username:</td>
                                <td>{props.user.userName}</td>
                            </tr>
                            <tr>
                                <td>S in thoi:</td>
                                <td>{props.user.phoneNumber}</td>
                            </tr>
                            <tr>
                                <td>Email:</td>
                                <td>{props.user.email}</td>
                            </tr>
                            <tr>
                                <td>Trng thi:</td>
                                <td>{props.user.status}</td>
                            </tr>
                            <tr>
                                <td>Nhn thng bo:</td>
                                <td>{props.user.getNotice ? "C" : "Khng"}</td>
                            </tr>
                        </tbody>
                    </table>                    
                </Modal.Body>
            </Modal>
        </div>
    );
};

export default DetailModalUser;